<!--eslint-disable-->
<template>
  <div class="servicos-wrapper">
    <div class="servicos">
      <div
        :id="servico.code"
        class="row"
        v-for="(servico, id) in servicos"
        :key="id"
        :class="{'lateral': servico.id % 2 === 0, 'lateralR': servico.id % 2 !== 0}"
      >
        <div class="wrapper">
          <div class :class="{'right': servico.id % 2 === 0, 'left': servico.id % 2 !== 0}">
            <div class="txt" v-html="servico.title"></div>
            <div class="line"></div>
          </div>
          <div :class="{'left': servico.id % 2 === 0, 'right': servico.id % 2 !== 0}">
            <div class="copy">
              <div class="text" v-html="servico.text"></div>
              <router-link :to="servico.link">
                <div class="btn blue">
                  <div class="txt">Saiba mais</div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="servicos-mobile">
      <div :id="servico.code" class="row" v-for="(servico, id) in servicos" :key="id">
        <div class="wrapper movement">
          <div class="top">
            <div class="copy">
              <div class="title">{{ servico.title }}</div>
              <div class="line"></div>
              <div class="text" v-html="servico.text"></div>
              <router-link :to="servico.link">
                <div class="btn blue">
                  <div class="txt">Saiba mais</div>
                </div>
              </router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import img from "../../assets/dummy-img.jpg";

export default {
  data() {
    return {
      img: img,
      servicos: [
        {
          id: 1,
          code: "avaliacao-desenvolvimento",
          title: "Avaliação de Desenvolvimento",
          text:
            "Avaliação especializada (dos 0 aos 6 anos), no âmbito da consideração dos diversos parâmetros do desenvolvimento infantil.",
          info:
            "Outras informações relacionadas com Avaliação de Desenvolvimento",
          img_src: img,
          link: "/servicos/avaliacao-desenvolvimento",
        },
        {
          id: 2,
          code: "avaliacao-psicopedagogica",
          title: "Avaliação Psicopedagógica",
          text:
            "Avaliação especializada (crianças/jovens) dos indicadores clínicos que condicionam o processo de aprendizagem e a proposta de intervenção psicopedagógica.",
          info: "Outras informações relacionadas com Avaliação Psicopedagógica",
          img_src: img,
          link: "/servicos/avaliacao-psicopedagogica",
        },
        {
          id: 3,
          code: "orientacao-vocacional",
          title: "Orientação Vocacional",
          text:
            "Avaliação especializada (jovens/adultos) das aptidões escolares e profissionais, confluindo com a avaliação dos parâmetros cognitivos, psicossociais e de personalidade.",
          info: "Outras informações relacionadas com Orientação Vocacional",
          img_src: img,
          link: "/servicos/orientacao-vocacional",
        },
        {
          id: 4,
          code: "despiste-dislexia",
          title: "Despiste de Dislexia",
          text:
            "Avaliação compreensiva das alterações específicas na leitura/escrita e das funções neuropsicológicas e neurolinguísticas que possam estar comprometidas.",
          info: "Outras informações relacionadas com Dislexia",
          img_src: img,
          link: "/servicos/despiste-dislexia",
        },
        {
          id: 5,
          code: "avaliacao-neuropsicologica",
          title: "Avaliação Neuropsicológica",
          text:
            "Avaliação especializada de perfis de funcionamento em diversas funções neurocognitivas (funções executivas, linguagem, memória, etc.), de perfis sócioafectivos (sintomatologia psicopatológica, personalidade) e de funcionalidade (autonomia/independência na vida diária).",
          info:
            "Outras informações relacionadas com Avaliação neuropsicológica",
          img_src: img,
          link: "/servicos/avaliacao-neuropsicologica",
        },
      ],
    };
  },
  methods: {
    scrollMeTo(refName) {
      var element = $("#" + refName);
      var top = element.offsetTop;

      window.scrollTo(0, top);
    },
  },
  mounted() {
    var location = window.location.href;
    var split_url = location.split("/");
    var ref = split_url[split_url.length - 1];

    if (ref.includes("#")) {
      var hash = ref.split("#");

      this.scrollMeTo(hash[1]);
    }
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquiose: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.servicos-wrapper {
  display: flex;
}

.servicos {
  height: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;

  @media only screen and (max-width: 991px) {
    display: none;
  }

  .row {
    height: pxToEm(530);
    padding: 0 pxToEm(130);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    &:nth-child(even) {
      background-color: #fafafa;

      .wrapper {
        max-width: pxToEm(1490);
        width: 100%;
        height: 100%;
        margin: auto;
        position: absolute;

        .left {
          position: absolute;
          width: pxToEm(770);
          // height: auto;
          min-height: pxToEm(200);
          background-color: #fff;
          border-radius: pxToEm(10);
          box-shadow: pxToEm(10) pxToEm(11) pxToEm(17) rgba(0, 0, 0, 0.1);
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          padding: pxToEm(40) pxToEm(60);
          transition: 0.5s;

          .copy {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: pxToEm(200);

            .title {
              font-size: pxToEm(26);
              color: $turquiose;
              font-family: "OS-Regular";
              font-weight: bold;
            }

            .line {
              width: pxToEm(80);
              height: pxToEm(1);
              background-color: $turquiose;
              margin-top: pxToEm(10);
            }

            .text {
              font-size: pxToEm(20);
              color: #707070;
              line-height: 1.5em;
              margin-top: pxToEm(10);
              font-family: "OS-Regular";
              margin-top: pxToEm(20);
            }

            .btn {
              width: pxToEm(131);
              height: pxToEm(44);
              align-self: flex-end;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                margin-bottom: pxToEm(20);
              }

              .txt {
                font-family: Arial, Helvetica, sans-serif;
                font-size: pxToEm(14);
                font-weight: bold;
              }
            }
          }
        }

        .right {
          width: pxToEm(590);
          height: pxToEm(290);
          background-color: $turquiose;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          padding: 0;
          box-shadow: none;
          border-radius: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .txt {
            color: $white;
            font-size: pxToEm(20);
            font-family: "OS-Bold";
            text-align: center;
            max-width: 90%;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: #fff;
            margin-top: pxToEm(10);
          }
        }
      }
    }

    &:nth-child(odd) {
      background-color: #fff;

      .wrapper {
        max-width: pxToEm(1490);
        width: 100%;
        height: 100%;
        margin: auto;
        position: absolute;

        .left {
          width: pxToEm(590);
          height: pxToEm(290);
          background-color: $turquiose;
          left: 0;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          border-radius: pxToEm(10);
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;

          .txt {
            color: $white;
            font-size: pxToEm(20);
            font-family: "OS-Bold";
            text-align: center;
            max-width: 90%;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: #fff;
            margin-top: pxToEm(10);
          }
        }

        .right {
          position: absolute;
          width: pxToEm(770);
          // height: auto;
          min-height: pxToEm(200);
          background-color: #fff;
          border-radius: pxToEm(10);
          box-shadow: pxToEm(10) pxToEm(11) pxToEm(17) rgba(0, 0, 0, 0.1);
          top: 50%;
          transform: translateY(-50%);
          right: 0;
          padding: pxToEm(40) pxToEm(60);
          transition: 0.5s;

          .copy {
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            min-height: pxToEm(200);

            .title {
              font-size: pxToEm(26);
              color: $turquiose;
              font-family: "OS-Regular";
              font-weight: bold;
            }

            .line {
              width: pxToEm(80);
              height: pxToEm(1);
              background-color: $turquiose;
              margin-top: pxToEm(10);
            }

            .text {
              font-size: pxToEm(20);
              color: #707070;
              line-height: 1.5em;
              margin-top: pxToEm(10);
              font-family: "OS-Regular";
              margin-top: pxToEm(20);
            }

            a {
              align-self: flex-end;
            }

            .btn {
              width: pxToEm(131);
              height: pxToEm(44);
              align-self: flex-end;

              @media only screen and (min-width: 300px) and (max-width: 599px) {
                margin-bottom: pxToEm(20);
              }

              .txt {
                font-family: Arial, Helvetica, sans-serif;
                font-size: pxToEm(14);
                font-weight: bold;
              }
            }
          }
        }
      }
    }
  }
}

.servicos-mobile {
  height: 100%;
  display: none;
  flex-direction: column;
  width: 100%;

  @media only screen and (max-width: 991px) {
    display: flex;
  }

  .row {
    height: auto;
    padding: 0 pxToEm(40);
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    // margin-bottom: pxToEm(40);

    @media only screen and (min-width: 600px) and (max-width: 767px) {
      padding: pxToEm(40) pxToEm(20);
    }

    @media only screen and (min-width: 300px) and (max-width: 599px) {
      padding: pxToEm(40) pxToEm(20);
    }

    &:nth-child(even) {
      background-color: #f3f3f3;
    }

    &:nth-child(odd) {
      background-color: #fff;
    }

    .wrapper {
      max-width: pxToEm(1490);
      width: 100%;
      height: 100%;
      margin: auto;
      display: flex;
      flex-direction: column;

      .top {
        width: 100%;
        height: auto;
        transition: 0.5s;
        padding: pxToEm(40) 0;
        display: flex;
        flex-direction: column;

        .copy {
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;

          .title {
            font-size: pxToEm(26);
            color: $turquiose;
            font-family: "OS-Regular";
            font-weight: bold;
            text-align: center;
          }

          .line {
            width: pxToEm(80);
            height: pxToEm(1);
            background-color: $turquiose;
            margin-top: pxToEm(10);
          }

          .text {
            font-size: pxToEm(20);
            color: #707070;
            line-height: 1.5em;
            margin-top: pxToEm(10);
            font-family: "OS-Regular";
            margin-top: pxToEm(20);
            text-align: center;
          }

          .btn {
            width: pxToEm(131);
            height: pxToEm(44);
            margin-top: pxToEm(50);

            @media only screen and (min-width: 300px) and (max-width: 599px) {
              margin-bottom: pxToEm(20);
            }

            .txt {
              font-family: Arial, Helvetica, sans-serif;
              font-size: pxToEm(14);
              font-weight: bold;
            }
          }
        }
      }

      .bottom {
        width: 100%;
        height: auto;
        background-color: $turquiose;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: pxToEm(20) 0;

        .txt {
          color: $white;
          font-size: pxToEm(20);
          font-family: "OS-Bold";
          text-align: center;
        }
      }
    }
  }
}
</style>



